.square {
    width: 100px;
    height: 100px;
    background: red;
}

.rectangle {
    width: 200px;
    height: 100px;
    background: red;
}

.circle {
    width: 100px;
    height: 100px;
    background: red;
    border-radius: 50%
}

.oval {
    width: 200px;
    height: 100px;
    background: red;
    border-radius: 100px / 50px;
}
